<template>
  <component :is="layout"></component>
</template>

<script>
import MainLayout from "./components/layouts/MainLayout.vue";
import EmptyLayout from "./components/layouts/EmptyLayout.vue";

export default {
  name: "App",
  components: {
    MainLayout,
    EmptyLayout,
  },
  computed: {
    layout() {
      return this.$store.state.user ? "main-layout" : "empty-layout";
    },
  },
};
</script>

<style lang="scss">
#app {
  width: 100%;
  height: 100%;
  font-family: $regular;
}
</style>
